<template><section><html><head></head><body><h1>流动线</h1>
<p><code v-pre="">vc-trail-polyline</code> 组件用于加载流动线效果。实质是通过<code v-pre="">vc-entity</code> 和 <code v-pre="">vc-graphics-polyline</code> 加载线，并赋予自定义材质 <code v-pre="">PolylineTrailMaterialProperty</code>实现。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>。</p>
<h2>示例</h2>
<h3>加载流动线效果</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <!-- 普通折线 -->
        <vc-trail-polyline :positions="positionsLine" color="yellow" image-url="./statics/SampleData/images/colors1.png" :width="5" ref="line" :loop="false" clamp-to-ground=""></vc-trail-polyline>
        <!-- 抛物线 -->
        <template v-for="(item, index) in positionsParabola">
          <vc-trail-polyline :key="'parabola' + index" :positions="item" color="yellow" image-url="./statics/SampleData/images/colors1.png" :width="5" ref="parabola"></vc-trail-polyline>
        </template>
        <!-- 抛物线 -->
        <template v-for="(item, index) in positionsParabola2">
          <vc-trail-polyline :key="'parabola2' + index" :positions="item" color="yellow" image-url="./statics/SampleData/images/colors.png" :width="2" ref="parabola"></vc-trail-polyline>
        </template>
        <template v-for="(item, index) in points2">
          <vc-entity :position="item">
            <vc-graphics-point :key="index" :color="item.color" :pixel-size="item.size" :disable-depth-test-distance="Number.POSITIVE_INFINITY"></vc-graphics-point>
          </vc-entity>
        </template>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
