        <span class="hljs-keyword">if</span> (<span class="hljs-title class_">Math</span>.<span class="hljs-title function_">abs</span>(options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lng</span> - options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lng</span>) &gt; <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">abs</span>(options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lat</span> - options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lat</span>)) {
