          <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; num; i++) {
            <span class="hljs-keyword">const</span> tempH = h - (<span class="hljs-title class_">Math</span>.<span class="hljs-title function_">pow</span>(-<span class="hljs-number">0.5</span> * L + <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">abs</span>(dlt) * i, <span class="hljs-number">2</span>) * <span class="hljs-number">4</span> * h) / <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">pow</span>(L, <span class="hljs-number">2</span>)
            <span class="hljs-keyword">const</span> lng = options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lng</span> + dlt * i
            <span class="hljs-keyword">const</span> lat = options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lat</span> + delLat * i
            result.<span class="hljs-title function_">push</span>({ lng, lat, <span class="hljs-attr">height</span>: tempH })
          }
        } <span class="hljs-keyword">else</span> {
          <span class="hljs-comment">//以 lat 为基准</span>
          <span class="hljs-keyword">let</span> dellng = (options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lng</span> - options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lng</span>) / num
          <span class="hljs-keyword">if</span> (options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lat</span> - options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lat</span> &gt; <span class="hljs-number">0</span>) {
            dlt = -dlt
          }
          <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; num; i++) {
            <span class="hljs-keyword">const</span> tempH = h - (<span class="hljs-title class_">Math</span>.<span class="hljs-title function_">pow</span>(-<span class="hljs-number">0.5</span> * L + <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">abs</span>(dlt) * i, <span class="hljs-number">2</span>) * <span class="hljs-number">4</span> * h) / <span class="hljs-title class_">Math</span>.<span class="hljs-title function_">pow</span>(L, <span class="hljs-number">2</span>)
            <span class="hljs-keyword">const</span> lng = options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lng</span> + dellng * i
            <span class="hljs-keyword">const</span> lat = options.<span class="hljs-property">startPoint</span>.<span class="hljs-property">lat</span> + dlt * i
            result.<span class="hljs-title function_">push</span>({ lng, lat, <span class="hljs-attr">height</span>: tempH })
          }
        }
        <span class="hljs-comment">// 落地</span>
        result.<span class="hljs-title function_">push</span>({ <span class="hljs-attr">lng</span>: options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lng</span>, <span class="hljs-attr">lat</span>: options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">lat</span>, <span class="hljs-attr">height</span>: options.<span class="hljs-property">endPoint</span>.<span class="hljs-property">height</span> || <span class="hljs-number">0</span> })
        <span class="hljs-keyword">return</span> result
      }
    }
  }
